.Close {
  display: none;
}

.Open {
  display: flex;
}

.darkOverlay {
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 110;
}

.Content {
  background-color: white;
  width: 500px;
  height: 400px;
  position: fixed;
  border-radius: 10px;
  top: 50%;
  left: 50%;
  margin-top: -250px; /* Negative half of height. */
  margin-left: -250px; /* Negative half of width. */
  z-index: 120;
  transition: transform 0.3s ease-out;
}

@media(max-width: 550px) {
  .Content {
    width: 100vw;
    margin-left: -50vw; /* Negative half of width. */
  }
}

.Content h1 {
  margin-left: 20px;
}
